$selectric-main-color:      #dbdbdb !default;
$selectric-sub-color : #f7f7f7 !default;                                           // Color used in border
$selectric-select-color : #666 !default;    
$selectric-text-color:      #333 !default;                                              // Color used in label
$selectric-bg-color:        #fff !default;                                           // Background color
$selectric-height:          30px !default;                                              // Outer height
$selectric-spacing:         9px !default;                                              // Label left padding
$selectric-border-width:    1px !default;                                               // Outer border width
$selectric-inner-height:    $selectric-height - ($selectric-border-width * 2) !default; // Inner height

.selectric-wrapper {
  position: relative;
  cursor: pointer;
}

.selectric-responsive {
  width: 100%;
}

.selectric {
  border: $selectric-border-width solid $selectric-main-color;
  background: $selectric-bg-color;
  position: relative;

  .label {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0 $selectric-inner-height 0 $selectric-spacing;
    font-size: 12px;
    line-height: $selectric-inner-height;
    color: $selectric-text-color;
    height: $selectric-inner-height;
    user-select: none;
  }

  .button {
    display: block;
    position: absolute;
    right: -1px;
    top: -1px;
    width: $selectric-inner-height;
    height: $selectric-inner-height;
    background: #F7F7F7;
    text-align: center;
    font: 0/0 a;
    /* *font: 20px/#{$selectric-inner-height} Lucida Sans Unicode, Arial Unicode MS, Arial; */
    border:1px solid $selectric-main-color;
    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      //@include sp-sk('arr_select');
    }
  }
}

.selectric-focus, .selectric-hover {
  .selectric {
    border-color: $selectric-select-color;
    .button {
      border-color: $selectric-select-color;
    }
  }
}

.selectric-open {
  z-index: 9999;

  .selectric {
    border-color: $selectric-select-color;
    .button {
      border-color: $selectric-select-color;
    }
  }

  .selectric-items {
    display: block;
  }
}

.selectric-disabled {
  /* filter: alpha(opacity=50);
  opacity: 0.5; */
  cursor: default;
  user-select: none;
  .selectric {
    background:$selectric-sub-color;
  }
}

.selectric-hide-select {
  position: absolute;
  overflow: hidden;
  top:0;
  left:0;
  width: 0;
  height: 0;

  select {
    position: absolute;
    left: -100%;
    display: none;
  }
}

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important;
}

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important;
}

/* Items box */
.selectric-items {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: $selectric-bg-color;
  border: 1px solid $selectric-main-color;
  z-index: -1;

  .selectric-scroll {
    // height: 100%;
    max-height:150px;
    overflow: auto;
    overflow-x:hidden;
  }

  .selectric-above & {
    top: auto;
    bottom: 100%;
  }

  ul, li {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 12px;
    line-height: 20px;
    min-height: 20px;
  }

  li {
    display: block;
    padding: 5px 9px;
    color: #666;
    cursor: pointer;

    &.selected {
      background: $selectric-sub-color;
      color: $selectric-text-color;
    }

    &:hover {
      background: $selectric-sub-color;
      color: $selectric-text-color;
    }
  }

  .disabled {
    filter: alpha(opacity=50);
    opacity: 0.5;
    cursor: default !important;
    background: none !important;
    color: #666 !important;
    user-select: none;
  }

  .selectric-group {
    .selectric-group-label {
      font-weight: bold;
      padding-left: 10px;
      cursor: default;
      user-select: none;
      background: none;
      color: #444;
    }

    &.disabled li {
      filter: alpha(opacity=100);
      opacity: 1;
    }

    li {
      padding-left: 25px;
    }
  }
}

.ofr, .ofl{
	.selectric-items{
		height:auto !important;
		li{
			overflow:hidden;
			white-space:nowrap;
		}
	}
}

.ofr{
	.selectric-items{
		left:auto;
		right:0;
	}
}

// dropbox
.dropbox {
  .in {
    position:relative;
    cursor:pointer;
  }
  .select {
    border: $selectric-border-width solid $selectric-main-color;
    background: $selectric-bg-color;
    position: relative;
      .label {
          display: block;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          margin: 0 $selectric-inner-height 0 $selectric-spacing;
          font-size: 12px;
          line-height: $selectric-inner-height;
          color: $selectric-text-color;
          height: $selectric-inner-height;
          user-select: none;
      }
      .button {
        display: block;
        position: absolute;
        right: -1px;
        top: -1px;
        width: $selectric-inner-height;
        height: $selectric-inner-height;
        background: #F7F7F7;
        text-align: center;
        font: 0/0 a;
        border:1px solid $selectric-main-color;
        &:after {
          content: "";
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          //@include sp-sk('arr_select');
        }
      }
      &:hover {
        border-color: $selectric-select-color;
        .button {
          border-color: $selectric-select-color;
        }
      }
  }
  .open {
    z-index:999;
    .select {
      border-color: $selectric-select-color;
      .button {
        border-color: $selectric-select-color;
      }
    }
    .option {
      display:block;
    }
  }
  .disabled {
    cursor: default;
    user-select: none;
    .select {
      background:$selectric-sub-color;
    }
  }
  // options
  .option {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background: $selectric-bg-color;
    border: 1px solid $selectric-main-color;
    z-index: -1;
    width:calc(100% - 2px);
    .option-scroll {
      max-height:155px;
      overflow: auto;
      overflow-x:hidden;
    }
    ul, li {
      list-style: none;
      padding: 0;
      margin: 0;
      font-size: 12px;
      line-height: 20px;
      min-height: 20px;
    }
    li {
      position:relative;
      .item {
        display: block;
        padding: 5px 9px;
        color: #666;
        cursor: pointer;
        &.selected, &:hover {
          background: $selectric-sub-color;
          color: $selectric-text-color;
        }
      }
      ul li .item {
        padding-left:30px;
      }
    }
    .chk {
      display:inline-block;
    }
    .icon {
      display:inline-block;
      vertical-align:top;
      margin-top:5px;
      &.fold {
        //@include sp-sk('icon_fold');
      }
      &.unfold {
        //@include sp-sk('icon_unfold');
      }
    }
  }
  
}


