@charset "UTF-8";

@media screen and (max-width:767px) {
    .main-wrap {
        min-width:auto;
    }

    // header
    .new-header {
        //display: none;
        width:100%;
        min-width:auto;
        border:none;
        &:before {
            display:none;
        }

        .header-container {
            position:relative;
            min-width:auto;
            height:56px;
            

            .header-logo {
                padding-left:15px;
                > a {
                    width:149px;
                    background:url('../images/mo-main_logo.gif') 0 0 no-repeat;
                    background-size:100% auto;
                }
            }

            .btn-all-menu {
                position:absolute;
                top:0; right:0;
                width:56px;
                height:100%;
                &:before {
                    display:inline-block;
                    width:24px;
                    height:18px;
                    background:url('../images/mo-icon_all_menu.gif') 0 0 no-repeat;
                    background-size:100% auto;
                    content:'';
                }
            }

            .gnb-wrap {
                display:none;
                flex:none;
                position: fixed;
                z-index: 1001;
                height: 100%;
                overflow-y: auto;
                top: 0px;
                right: 0;
                width: 100%;
                margin:0;
                
                &:before {
                    display:block;
                    position:fixed;
                    top:0; left:0; right:0; bottom:0;
                    width:100%;
                    height:100%;
                    background:rgba(0,0,0,.5);
                    content:'';
                }

                &.open {
                    display:block;
                }
                
                .mo-header-util {
                    width: calc(100% - 10%);
                    height:56px;
                    margin-left:10%;
                    position:relative;
                    z-index:1;
                    background:#414a69;
                    overflow:hidden;

                    .btn-global {
                        position: relative;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        display: -webkit-flex;
                        align-items: center;
                        justify-content:center;
                        float:left;
                        width:110px;
                        height:100%;
                        color:#fff;
                        font-size:12px;
                        text-align:center;
                        span {
                            margin:0 15px 0 10px;
                        }
                        &:before {
                            width:19px;
                            height:19px;
                            background:url('../images/mo-icon_global.gif') 0 0 no-repeat;
                        }
                        &:after {
                            margin-top:6px;
                            width:10px;
                            height:16px;
                            background:url('../images/mo-icon_arr_global.gif') 0 0 no-repeat;
                        }
                        &:before {
                            display:inline-block;
                            background-size:100% auto;
                            content:'';
                        }
                    }
                    .btn-sitemap {
                        color:#fff;
                        font-size:12px;
                        line-height:56px;
                        padding:0 14px 0 0;
                        &:before {
                            vertical-align:-2px;
                            display:inline-block;
                            margin-right:13px;
                            width:1px;
                            height:13px;
                            border-left:1px solid #7a8196;
                            content:'';
                        }
                    }

                    .btn-all-close {
                        float:right;
                        width:56px;
                        height:100%;
                        &:before {
                            display:inline-block;
                            width:24px;
                            height:24px;
                            background:url('../images/mo-btn_all_close.gif') 0 0 no-repeat;
                            background-size:100% auto;
                            content:'';
                        }
                    }
                }
                .gnb-menu {
                    position:relative;
                    z-index:1;
                    transition: all .5s ease-in-out;

                    // 1depth
                    .depth1-list {
                        flex:none;
                        display:block;
                        position: relative;
                        width: calc(100% - 10%);
                        height:calc(100vh - 56px);
                        margin-left:10%;
                        background: #f9f9f9;
                        overflow-x: hidden;
                        overflow-y: auto;

                        .depth1-menu {
                            display: table;
                            width: 100%;
                            > a {
                                display: block;
                                padding: 15px 0px 15px 20px;
                                width: 110px;
                                height:auto;
                                color: #4a4a4a;
                                text-align: left;
                                background: #f9f9f9;
                                font-size: 13px;
                                border-bottom:1px solid #dbdbdb;
                                position: relative;
                                font-weight: 600;
                                &:before {
                                    display:none;
                                }
                                /* &:hover, */ 
                                &:focus,
                                &.active {
                                    background:#fff;
                                    &:after {
                                        content:'';
                                        position:absolute;
                                        right:-1px; top:0;
                                        width:1px;
                                        height:100%;
                                        border-right:1px solid #fff;
                                        z-index:2;
                                    }
                                    ~ .sub-depth-wrap {
                                        display:block;
                                    }
                                }
                            }
                        }
                        .sub-depth-wrap {
                            display: none;
                            position: absolute;
                            left: 110px;
                            top: 0;
                            height:100%;
                        }

                        // 2depth
                        .depth2-list {
                            flex:none;
                            display:block;
                            width:auto;
                            padding:0;
                            margin:0;
                            width: calc(100% - 110px);
                            height: 100%;
                            text-align: left;
                            border-left:1px solid #dbdbdb;

                            .depth2-menu {
                                width:100%;
                                min-width:auto;
                                border:none;
                                padding:0;
                                > a {
                                    position:relative;
                                    height:50px;
                                    line-height:50px;
                                    padding:0 30px;
                                    font-size:14px;
                                    &.more {
                                        &:after {
                                            position:absolute;
                                            top:50%; right:15px;
                                            transform: translateY(-50%);
                                            width:11px;
                                            height:7px;
                                            background:url('../images/mo-arr_gnb.gif') 0 0 no-repeat;
                                            background-size: 100%;
                                            content:'';
                                        }
                                    }
                                }
                                &.open {
                                    a.more {
                                        &:after {
                                            transform: rotate(-180deg);
                                            margin-top:-5px;
                                        }
                                    }
                                    .depth3-list {
                                        display:block;
                                    }
                                }
                            }
                        }

                        // 3depth
                        .depth3-list {
                            display:none;
                            margin:0;
                            .depth3-menu {
                                > a {
                                    padding:0 30px;
                                    height:30px;
                                    line-height:30px;
                                    font-size:12px;
                                }
                            }
                        }

                        
                    }
                }
            }
        }
        
    }

    // contents
    .main-container {
        background:#eaeaea;
        padding-bottom:35px;
        .main-visual-wrap {
            height:auto;

            .swiper-slide {
                width:100%;
                height:440px;
                background-position: 50% 0;
                background-repeat:no-repeat;
                background-size: cover;
                
                &.visual-slide1 {
                    background-image: url('../images/mo-main_visual1.jpg');
                }
                &.visual-slide2 {
                    background-image: url('../images/mo-main_visual2.jpg');
                }
                &.visual-slide3 {
                    background-image: url('../images/mo-main_visual3.jpg');
                }
            }
            .swiper-inner {
                width:100%;
                height:100%;
                padding:0 40px;
                .desc {
                    padding-top:50px;
                    .tit {
                        font-size:30px;
                    }
                    .txt {
                        margin-top:5px;
                        font-size:14px;
                        line-height:24px;
                        .tx-block {
                            display:inline-block;
                        }
                    }
                }
                .txt-caption {
                    top:auto; 
                    left:0; right:0;
                    bottom:80px;
                    width:100%;
                }
            }
            .btn-swiper-wrap {
                width:100%;
                top:180px;
                left:0;
                transform: none;
            }
        }

        .main-banner-wrap {
            margin-top:-80px;
            padding-bottom:80px;
            .main-banner-inner {
                top:0;    
                .banner-lists {
                    width:100%;
                    height:auto;
                    margin:0;
                    padding:20px 15px;
                    flex-direction: row;
                    flex-flow: row wrap;
                }
                .item {
                    height:auto;
                    flex:none;
                    margin:0;
                    
                    @each $mask-name in satellite, chol, data, specific, high, field {
                        &-#{$mask-name} {
                            background-image:url('../images/mo-icon_bnr_#{$mask-name}.png');
                            background-size:100%;
                            background-position-y: 0;
                            position:relative;
                            width:calc(50% - 10px);
                            margin:0 5px 10px;

                            &:before {
                                display:block;
                                padding-bottom:100%;
                                content:'';
                            }
                            > a {
                                position:absolute;
                                left:0; top:0;
                                width:100%;
                                height:100%;
                            }
                            h3.tit {
                                padding-top:70%;
                            }
                        }
                    }
                    &:nth-of-type(even) {
                        top:20px;
                    }

                    // 공지사항
                    &-notice {
                        margin: 30px auto 20px;
                        width: calc(100% - 10px);
                        padding:20px 23px 25px;

                        .notice-link {
                            line-height: 1.2;
                            .noti-tit {
                                margin-top:12px;
                                font-size:14px;
                            }
                            .noti-date {
                                font-size:11px;
                            }
                            .noti-txt {
                                font-size:12px;
                            }
                        }
                        .btn-swiper-wrap {
                            margin-top:15px;
                        }
                    }

                    // sns
                    &-sns {
                        top:0 !important;
                        margin:0 auto;
                        width: calc(100% - 10px);
                        padding:25px;
                        .txt {
                            margin-top:15px;
                            font-size:12px;
                            line-height:1.2;
                        }
                        .item-sns-link {
                            margin-top:15px;
                            height:44px;
                            background:url('../images/mo-icon_bnr_sns.png') 50% 0 no-repeat;
                            background-size: auto 44px;
                            text-align:center;
                            font-size:0;
                            > a {
                                display:inline-block;
                                width:44px;
                                height:44px;
                                margin:0 15px;
                            }
                        }
                    }

                    h3.tit {
                        font-size:15px;
                        > span {
                            font-size:12px;
                        }
                    }
                }
            }
        }
    }
    
    // footer
    .new-footer {
        min-width:auto;
        
        .site-links {
            min-width:auto;
            border:1px solid #dbdbdb;
            border-width:1px 0 0;
            .site-link-container {
                flex-direction: row;
                flex-wrap: wrap;
                .site-link {
                    width:50%;
                    height:40px;
                    border-bottom:1px solid #dbdbdb;
                    border-width:0 0 1px;
                    &:nth-of-type(odd) {
                        border-right:1px solid #dbdbdb;
                    }
                    > a {
                        padding:0 20px 0 10px;
                        line-height:38px;
                        font-size:12px;
                        &:after {
                            right:10px;
                            background: url('../images/mo-icon_arr_site.png') 0 0 no-repeat;
                            background-size:100% auto;
                        }
                    }
                    > ul {
                        bottom:39px;
                        max-height:142px;
                        li {
                            line-height:35px;
                            a {
                                font-size:12px;
                            }
                        }
                    }
                    
                }
            }
        }

        .footer-container {
            display:block;
            flex:none;
            padding:40px 20px;
            .footer-content {
                padding:0;
                flex:none;

                .footer-menu {
                    padding:0 0 30px;
                    > a {
                        padding:4px 20px;
                        
                        font-size:12px;
                        &:before {
                            top:7px;
                            width:1px; height:11px;
                        }
                    }
                }
            }
            address {
                font-size:11px;
                word-break: keep-all;
                .tx-copy {
                    margin-top:15px;
                }
            }
            .footer-banner  {
                margin-top:25px;
                text-align: left;
                .banner {
                    margin:0 10px 0 0;
                }
                .link1 {
                    width:130px; height:48px;
                    background: url('../images/mo-footer_banner1.gif') 0 0 no-repeat 100% auto;    
                }
                .link2 {
                    width:142px; height:48px;
                    background: url('../images/mo-footer_banner2.gif') 0 0 no-repeat 100% auto;    
                }
                
            }
        }
    }

}

/* mobile ui */
@media screen and (max-width:767px) {
    .main-wrap {
        .pc-use {
            display: none !important
        }
    }
    
}

/* pc ui */
@media screen and (min-width:768px) {
    .main-wrap {
        .mo-use {
            display: none !important
        }
    }
    
}



