@charset "UTF-8";

/* s:footer */
.new-footer {
    position: relative;
    z-index:2;
    width:100%;
    min-width:$pc-width;
    background:#292929;

    .site-links {
        position: relative;
        z-index:3;
        min-width:$pc-width;
        background:#fff;
        border-top:1px solid #dbdbdb;
        .site-link-container {
            max-width:$pc-width;
            width:100%;
            margin:0 auto;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            display: -webkit-flex;
            .site-link {
                position:relative;
                width:calc(100% / 4);
                height:60px;
                border-right:1px solid #dbdbdb;
                &:first-child {
                    border-left:1px solid #dbdbdb;
                }
                > a {
                    display:block;
                    position:relative;
                    width:100%; height:100%;
                    padding:0 40px 0 20px;
                    line-height:58px;
                    font-size:16px;
                    &:after {
                        position:absolute;
                        top:50%;
                        right:20px;
                        transform: translateY(-50%);
                        display:block;
                        width:14px; height:8px;
                        background: url('../images/icon_arr_site.png') 0 0 no-repeat;
                        content:'';
                    }
                }
                > ul {
                    display:none;
                    position:absolute;
                    left:-1px; bottom:60px;
                    overflow-x:hidden;
                    overflow-y:auto;
                    width:calc(100% + 2px);
                    max-height:234px;
                    border:1px solid #dbdbdb;
                    background:#fff;
                    li {
                        line-height:58px;
                        a {
                            display:block;
                            padding:0 20px;
                            font-size:16px;
                            @include ellipsis(false, false, false);
                            &:hover, &:focus {
                                background:#f9f9f9;
                                color:#3c7fb7;
                                text-decoration: underline;
                            }
                        }
                    }
                    &.open {
                        display:block;
                    }
                }
            }
        }
    }

    .footer-container {
        max-width:$pc-width;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        width:100%;
        margin:0 auto;

        .footer-content {
            padding:50px 0 40px;
            flex:1;
            color:#999;

            .footer-menu {
                padding:0 0 25px;
                > a {
                    position: relative;
                    display:inline-block;
                    padding:0 24px 0 30px;
                    font-size:14px;
                    color:#999;
                    &:before {
                        position:absolute;
                        left:0; top:0;
                        display: inline-block;
                        width:1px; height:13px;
                        border-left:1px solid #3f3f3f;
                        content:'';
                    }

                    &:first-child {
                        padding-left:0;
                        &:before {
                            display: none;;
                        }
                    }
                }
            }
            address {
                line-height:1.7;
                .tit {
                    color:#fff;
                }
                .foot-mail {
                    color:#999;
                    text-decoration: underline;
                }
            }
            .tx-noti {
                margin-top:20px;
            }
        }

        .footer-banner {
            width:300px;
            text-align: right;
            font-size:0;
            
            .banner {
                display:inline-block;
                margin-left:5px;
            }
            .link1 {
                width:130px; height:48px;
                background: url('../images/footer_banner1.gif') 0 0 no-repeat;    
            }
            .link2 {
                width:142px; height:48px;
                background: url('../images/footer_banner2.gif') 0 0 no-repeat;    
            }
        }
    }
}
/* //e:footer */

