@mixin ellipsis {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}
@mixin display($type: null, $float: null, $a: null, $w: null, $h: null, $minw: null, $maxw: null, $minh: null, $maxh: null) {
	display: $type;
	float: $float;
	@if $a != null {
		width: $a;
		height: $a;
	} @else {
		width: $w;
		height: $h;
	}
	min-width: $minw;
	max-width: $maxw;
	min-height: $minh;
	max-height: $maxh;
}
@mixin font($f: null, $l: null, $c: null, $s: null, $w: null, $a: null) {
	color: $c;
	font-family: $f;
	font-size: $s;
	font-weight: $w;
	text-align: $a;
	line-height: $l;
}
@mixin pos($p: null, $t: null, $r: null, $b: null, $l: null, $z: null, $a: null) {
	position: $p;
	z-index: $z;
	@if $a != null {
		top: $a;
		right: $a;
		bottom: $a;
		left: $a;
	} @else {
		top: $t;
		right: $r;
		bottom: $b;
		left: $l;
	}
}
@mixin arrow($dir: null, $w: null, $h: null, $c: null) {
	border: $w/2 solid transparent;
	@if $dir == down {
		border-top: $h solid $c;
		border-bottom: 0;
	} @else if $dir == up {
		border-bottom: $h solid $c;
		border-top: 0;
	} @else if $dir == left {
		border-right: $h solid $c;
		border-left: 0;
	} @else if $dir == right {
		border-left: $h solid $c;
		border-right: 0;
	} 
}
@mixin bul($w: null, $h: null, $t: null, $r: null, $c: null) {
	&:after {
		content: "";
		position: absolute;
		top: $t; left: 0;
		width: $w; height: $h;
		border-radius: $r;
		background: $c;
	}
}
@mixin clear() {
	&:after {
		content: "";
		display: block;
		clear: both;
	}
}
@mixin align() {
	&:after {
		content: "";
		display: inline-block;
		height: 100%;
		vertical-align: middle;
	}
}
@mixin blind() {
	position: absolute;
	height: 1px; width: 1px;
	overflow: hidden; 
	clip: rect(1px,1px,1px,1px); clip: rect(1px 1px 1px 1px);
	word-break: initial;
	word-wrap: initial;
}
@mixin convert($attr, $t) {
	#{$attr}: calc(100vw * #{$t} / 720);
}
// 한줄 말줄임표
@mixin ellipsis($autoWidth: true, $isInline: true, $clearLineHeight: true) {
	@if ($isInline) {
		display: inline-block;
	}
	@if ($autoWidth) {
		width: 100%;
	}
	max-width: 100%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	@if ($clearLineHeight) {
		line-height: normal;
	}

	// 멀티 라인 값 클리어
	@include clear_multi_ellipsis;
}

// n줄 말줄임표
@mixin multi_ellipsis($line-height, $lines: 2, $useHeight: true) {
	overflow: hidden;
	@if $useHeight {
		height: $line-height * $lines;
	}
	max-height: $line-height * $lines;
	line-height: $line-height;

	display: block;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: $lines;
	line-clamp: $lines;

	text-overflow: -o-ellipsis-lastline;

	white-space: normal;
	word-break: break-word;
}

@mixin clear_multi_ellipsis {
	display: inherit;
	height: auto;
	max-height: none;
	-webkit-box-orient: inherit;
	-webkit-line-clamp: inherit;
}
