@charset "UTF-8";

.main-wrap {
    min-width:$pc-width;

    .main-container {
        position: relative;
        z-index:1;
        background:#eaeaea;
    }

    // main visual
    .main-visual-wrap {
        width:100%;
        height:500px;
        
        .swiper-slide {
            width:100%;
            height:500px;
            background-position: 50% 0;
            background-repeat:no-repeat;
            
            &.visual-slide1 {
                background-image: url('../images/main_visual1.jpg');
            }
            &.visual-slide2 {
                background-image: url('../images/main_visual2.jpg');
            }
            &.visual-slide3 {
                background-image: url('../images/main_visual3.jpg');
            }
        }
        .swiper-inner {
            position:relative;
            width:$pc-width;
            margin:0 auto;
            padding:0 40px;
            .desc {
                padding-top:125px;
                font-weight:400;
                color:#fff;
                
                .tit {
                    display:block;
                    font-size:40px;
                    font-weight:400;
                }
                .txt {
                    display:block;
                    margin-top:5px;
                    font-size:16px;
                    line-height:24px;
                    font-weight:300;
                    word-break: keep-all;
                    .tx-block {
                        display:block;
                    }
                }
            }
            
            .txt-caption {
                position:absolute;
                top:430px;
                right:-280px;
                width:280px;
                padding-left:25px;
                color:#fff;
                font-size:14px;
                text-shadow: 1px 0px #000, 1px 1px #000, 1px -1px #000;
            }
        }
        .btn-swiper-wrap {
            position:absolute;
            top:260px; left:50%;
            z-index:1;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            display: -webkit-flex;
            width:$pc-width;
            transform:translateX(-50%);
            padding:0 40px;

            button {
                display:inline-block;
                width:20px;
                height:20px;
                font-size:1;
            }
            .btn-swiper-arrow {
                &:after {
                    display:inline-block;
                    width:20px;
                    height:10px;
                    background:url('../images/icon_arr_visual.png') no-repeat;
                    content:'';
                }
                &.prev {
                    &:after {
                        background-position: 0 0;
                    }
                }
                &.next {
                    margin-right:10px;
                    &:after {
                        background-position: -20px 0;
                    }
                }
                &.pause {
                    &:after {
                        width:7px;
                        background-position: -40px 0;
                    }
                    /* 20210524 추가 */
                    &.play_active {
                        &:after {
                            width:10px;
                            background-position: -50px 0;
                        }
                    }
                }
                &.play {
                    &:after {
                        width:10px;
                        background-position: -50px 0;
                    }
                }
            }
            .swiper-pagination {
                position:static;
                width:55px;
                color:#fff;
                font-size:12px;
            }
        }

        // 블랙 폰트 일 경우
        .slide-black {
            .swiper-inner {
                .desc {
                    color:#111;
                }
            }
        }
    }

    // main banner
    .main-banner-wrap {
        width:100%;
        background:#eaeaea;

        .main-banner-inner {
            position:relative;
            top:-115px;
            z-index:2;
            //min-width: $pc-width;
            max-width:$pc-width;
            width:100%;
            margin:0 auto -115px;
        
            .banner-container {
                overflow: hidden;
            }
            .banner-lists {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                display: -webkit-flex;
                width: $pc-width + 20px;
                height: 750px;
                margin-left:-10px;
                flex-flow: column wrap;
                align-content: flex-start;
            }

            .item {
                width:280px;
                height:280px;
                flex: 0 0 280px;
                margin:0 10px 20px;
                background-color:#fff;
                background-position-x:50%;
                background-repeat:no-repeat;

                $barColor1 : #3c7fb7;
                $barColor2 : #414a69;
                $barColor3 : #d94c58;
                $barColor4 : #2c9798;

                &-satellite {
                    background-color: $barColor1;
                    color:#fff;
                    background-position-y: 62px;
                }
                &-chol {
                    border:2px solid $barColor1;
                    color: $barColor1;
                    background-position-y: 57px;
                }
                &-data {
                    margin-top:30px;
                    border:2px solid $barColor2;
                    color: $barColor2;
                    background-position-y: 66px;
                }
                &-specific {
                    background-color:$barColor2;
                    color:#fff;
                    background-position-y: 64px;
                }
                &-high {
                    background-color:$barColor3;
                    color:#fff;
                    background-position-y: 76px;
                }
                &-field {
                    border:2px solid $barColor3;
                    color: $barColor3;
                    background-position-y: 62px;
                }
                &-notice {
                    margin-top:30px;
                    border:2px solid $barColor4;
                    color:#4a4a4a;
                }
                &-sns {
                    background-color:$barColor4;
                    color:#fff;
                }

                @each $mask-name in satellite, chol, data, specific, high, field {
                    &-#{$mask-name} {
                        background-image:url('../images/icon_bnr_#{$mask-name}.png');
                        
                        text-align:center;
                        > a {
                            display:block;
                            width:100%;
                            height:100%;
                        }
                        h3.tit {
                            padding-top:200px;
                        }
                    }
                }

                // 공지사항
                &-notice {
                    position:relative;
                    padding:35px 28px 0;
                    -ms-flex:0 0 250px;

                    .icon-new {
                        position:absolute;
                        right:0; top:0;
                        display:block;
                        width:26px; height:26px;
                        background:url('../images/icon_notice_new.png') 0 0 no-repeat;
                    }
                    .notice-link {
                        display:block;
                        width:100%; height:100%;
                        color:#4a4a4a;
                        word-break:keep-all;

                        [class^="noti-"] {
                            display:block;
                        }
                        .noti-tit {
                            margin-top:15px;
                            font-size:16px;
                            font-weight:600;
                        }
                        .noti-date {
                            margin-top:5px;
                            font-size:12px;
                        }
                        .noti-txt {
                            margin-top:10px;
                            font-size:14px;
                            @include multi_ellipsis(20px, 2);
                        }
                    }

                    // s:slide button
                    .btn-swiper-wrap {
                        margin-top:20px;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        display: -webkit-flex;
            
                        button {
                            display:inline-block;
                            width:29px;
                            height:30px;
                            font-size:1;
                        }
                        .btn-swiper-arrow {
                            &:after {
                                display:inline-block;
                                width:100%;
                                height:100%;
                                background:url('../images/icon_arr_notice.png') no-repeat;
                                content:'';
                            }
                            &.prev {
                                width:30px;
                                &:after {
                                    background-position: 0 0;
                                }
                            }
                            &.next {
                                &:after {
                                    background-position: -30px 0;
                                }
                            }
                            /* 20210524 추가 */
                            &.pause {
                                &:after {
                                    background-position: -59px 0;
                                }
                                &.play_active {
                                    &:after {
                                        background-position: -88px 0;
                                    }
                                }
                            }
                            &.play {
                                &:after {
                                    background-position: -88px 0;
                                }
                            }
                        }
                    }
                    // e:slide button
                }

                // sns
                &-sns  {
                    padding:35px 28px 0;
                    -ms-flex:0 0 250px;
                    word-break:keep-all;
                    .txt {
                        margin-top:25px;
                        font-size:16px;
                        line-height:1.4;
                    }
                    .item-sns-link {
                        margin-top:40px;
                        height:70px;
                        background:url('../images/icon_bnr_sns.png') 50% 0 no-repeat;
                        text-align:center;
                        font-size:0;
                        > a {
                            display:inline-block;
                            width:72px;
                            height:70px;
                            margin:0 10px;
                        }
                    }
                }

                h3.tit {
                    font-size:22px;
                    font-weight:600;
                    > span {
                        display:block;
                        font-size:18px;
                        font-weight: 300;
                    }
                }

            }
        }
    }

}

