@charset "UTF-8";

/* s:header */
.new-header {
    position: relative;
    z-index: 3;
    width:100%;
    min-width:$pc-width;
    border-top:2px solid #3c7fb7;
    border-bottom:1px solid #dbdbdb;
    background:#fff;
    &:before {
        position:absolute;
        top:-2px;
        left:50%;
        transform:translateX(-50%);
        width:$pc-width;
        border-top:2px solid #d94c58;
        content:'';
    }

    .header-container {
        //width:$pc-width;
        max-width:$pc-width;
        width:100%;
        height:100px;
        margin:0 auto;

        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        display: -webkit-flex;
        align-items: center;

        .header-logo {
            > a {
                display:block;
                width:178px;
                height:32px;
                background:url('../images/main_logo.gif') 0 0 no-repeat;
            }
        }

        // s: gnb
        .gnb-wrap {
            flex:1;
            //margin-left:85px;
            margin-left:60px;

            .gnb-menu {

                .slogan {
                    display: none;
                    position: absolute;
                    top: 140px;
                    z-index: 2;
                    width: 265px;
                    height: 200px;
                    padding-top:10px;
                    margin-left: -265px;
                    background:url('../images/bg_slogan.gif') 0 0 no-repeat;
                    .tit {
                        display:block;
                        margin-bottom:15px;
                        font-size:40px;
                        line-height:1.3;
                        strong {
                            display:block;
                            font-weight:700;
                        }
                    }
                    .txt {
                        font-size:16px;
                    }
                }

                // 1depth
                .depth1-list {
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    display: -webkit-flex;
                    align-items: center;

                    .depth1-menu {
                        //padding:0 25px;

                        > a {
                            position: relative;
                            display:block;
                            height:100px;
                            padding:35px 25px;
                            font-size:24px;
                            font-weight:600;
                            color:$main-color;
                            white-space: nowrap;
                            &:hover, &:focus,
                            &.active {
                                color:#2169a5;
                                &:before {
                                    position:absolute;
                                    left:25px; bottom:-1px;
                                    width:calc(100% - 50px);
                                    border-bottom:2px solid #2169a5;
                                    content:'';
                                }
                            }
                        }
                        
                        &:first-child {
                            padding-left:0;
                        }
                    }

                    .sub-depth-wrap {
                        display:none;
                        width:100%;
                        z-index:1;
                        position:absolute;
                        top:101px;
                        left:0;
                        background:#fff;

                        &.open {
                            display:block;
                        }
                    }

                    // 2depth
                    .depth2-list {
                        width: $pc-width;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        display: -webkit-flex;
                        min-height:290px;
                        padding:40px 0;
                        margin:0 auto;
                        padding-left:265px;
                        
                        .depth2-menu {
                            position: relative;
                            //z-index:4;
                            width:auto;
                            min-width:calc(100% / 6);
                            padding:0 20px;
                            border-left:1px solid #dbdbdb;
                            &.wide-split {
                                // width:calc(100% / 3);
                                width:auto;
                            }
                            > a {
                                display:block;
                                font-size:20px;
                                font-weight:600;
                            }   
                        }
                    }

                    // 3depth
                    .depth3-list {
                        margin-top:10px;

                        .depth3-menu {
                            font-size:14px;
                            line-height:30px;
                            a {
                                display: block;    
                            }
                            a:hover, a:focus,
                            .active {
                                color:#2169a5;
                                text-decoration: underline;
                            }
                        }
                    }
                }
            }
        }
        // e: gnb

        .header-util-wrap {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            display: -webkit-flex;
            align-items: center;

            .util-menu {
                height:20px;
                border-left:1px solid #dbdbdb;
                padding-left:15px;
                //margin-left:15px;
                .btn-global {
                    position: relative;
                    span {
                        display:inline-block;
                        margin:0 10px;
                    }
                    &:before {
                        vertical-align: top;
                        display:inline-block;
                        content:'';
                    }
                    &:before {
                        width:19px; height:19px;
                        background:url('../images/icon_global.gif') 0 0 no-repeat;
                    }
                    &:after {
                        margin:7px 15px 0 0;
                        width:10px; height:6px;
                        background:url('../images/icon_arr_global.gif') 0 0 no-repeat;
                    }
                }

                .btn-sitemap {
                    display:inline-block;
                    width:30px;
                    line-height:1;
                    &:before {
                        margin-top:3px;
                        display:inline-block;
                        width:20px; height:14px;
                        background:url('../images/icon_all_menu.gif') 0 0 no-repeat;
                        content:'';
                    }
                }
            }
        }
    }
}
/* //e:header */